import { Route } from '@angular/compiler/src/core';
import { Component, Input, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UsuarioService } from 'src/app/services/usuario.service';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';

@Component({
  selector: 'app-form-user',
  templateUrl: './form-user.component.html',
  styleUrls: ['./form-user.component.scss']
})
export class FormUserComponent implements OnInit {

  @Input()
  public userSelect: any;
  public title: string;
  public userForm: FormGroup;
  public isPassCorrect: boolean;
  public roles: any [];
  private mensajes: MensajesComponent = new MensajesComponent(this.router);
  
  constructor(private userService: UsuarioService, private router: Router) { }

  ngOnInit(): void {
    this.roles = [];
    this.userService.getAllRoles().subscribe(
      response => {
        this.roles = response;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajes.mensajesError(error);
      }
    );
    if(this.userSelect) {
      this.title = "Actualizar usuario: " + this.userSelect.nombre + " " + this.userSelect.apellido;
      console.log(this.userSelect);
      this.userForm = new FormGroup({
        "rolId": new FormControl("", Validators.required),
        "email": new FormControl(this.userSelect.correo, [Validators.email, Validators.required, Validators.maxLength(100)]),
        "nickname": new FormControl(this.userSelect.apodo, [Validators.required, Validators.maxLength(20), Validators.minLength(3)]),
        "name": new FormControl(this.userSelect.nombre, [Validators.required, Validators.maxLength(20), Validators.minLength(3)]),
        "lastname": new FormControl(this.userSelect.apellido, [Validators.required, Validators.maxLength(20), Validators.minLength(3)]),
        "direction": new FormControl(this.userSelect.direccion, [Validators.required, Validators.maxLength(200), Validators.minLength(5)]),
        "phone": new FormControl(this.userSelect.telefono, [Validators.required, Validators.min(1111111111), Validators.max(9999999999)]),
        "password": new FormControl("", [Validators.required, Validators.maxLength(10), Validators.minLength(5)]),
        "vpassword": new FormControl("", [Validators.required, Validators.maxLength(10), Validators.minLength(5)])
      });
    } else {
      this.title = "Crear usuario";
      this.userForm = new FormGroup({
        "rolId": new FormControl("", Validators.required),
        "email": new FormControl("", [Validators.email, Validators.required, Validators.maxLength(100)]),
        "nickname": new FormControl("", [Validators.required, Validators.maxLength(20), Validators.minLength(3)]),
        "name": new FormControl("", [Validators.required, Validators.maxLength(20), Validators.minLength(3)]),
        "lastname": new FormControl("", [Validators.required, Validators.maxLength(20), Validators.minLength(3)]),
        "direction": new FormControl("", [Validators.required, Validators.maxLength(200), Validators.minLength(5)]),
        "phone": new FormControl("", [Validators.required, Validators.min(1111111111), Validators.max(9999999999)]),
        "password": new FormControl("", [Validators.required, Validators.maxLength(10), Validators.minLength(5)]),
        "vpassword": new FormControl("", [Validators.required, Validators.maxLength(10), Validators.minLength(5)])
      });
    }
  }

}
